// Override Bootstrap's Sass default variables
//
// Nearly all variables in Bootstrap are written with the `!default` flag.
// This allows you to override the default values of those variables before
// you import Bootstrap's source Sass files.
//
// Overriding the default variable values is the best way to customize your
// CSS without writing _new_ styles. For example, change you can either change
// `$body-color` or write more CSS that override's Bootstrap's CSS like so:
// `body { color: red; }`.

//https://www.a11yproject.com/
//https://github.com/squizlabs/HTML_CodeSniffer
//https://www.tpgi.com/color-contrast-checker/
//@import "bootstrap";
//https://github.com/webpack-contrib/sass-loader
//@import "~bootstrap"; //from node modules


html {
    box-sizing: border-box;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

$my-yellow: #e9c46aff;
$my-orange: #e86a58ff;
$my-green: #1a6a62ff;
$my-blue1: #023047ff;
$my-blue2: #264653ff;
$my-dark-gray: #3b373aff;
$my-darker-gray: rgb(34, 32, 35);
$my-gray: #4d494cff;
$my-light-gray: #9e9c9bff;
$my-off-white: #eeeeeeff;
$primary-color: $my-blue1;

$body-bg: $my-off-white;
$body-color: $my-darker-gray;
$theme-colors: ("primary": $my-blue1,
"danger": $my-orange);

